.header {
  font-size: 8rem;
  padding-bottom: 1rem;
  width: 0;
  border-bottom: 1px solid gray;
  display: flex;
  align-items: baseline;
  position: relative;
  animation: border-100 1.5s linear forwards;
}

@keyframes border-100 {
  0% {
  }
  100% {
    width: 100%;
  }
}

.sub-title {
  margin-top: 10rem;
  font-size: 5rem;
  padding-bottom: 1rem;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}
